import { useRequest } from "ahooks";
import Title from "antd/es/typography/Title";
import { useParams } from "react-router-dom";
import { getComponentStatService } from "../../../services/stat";
import { useEffect, useState } from "react";
import { getComponentConfByType } from "../../../components/QuestionComponents";

type PropsType = {
  selectComponentId: string;
  selectComponentType: string;
};
const ChartStat = (props: PropsType) => {
  const { selectComponentId, selectComponentType } = props;
  const { id = "" } = useParams();
  const [stat, setStat] = useState([]);
  const { run } = useRequest(
    async (questionId, selectComponentId) =>
      await getComponentStatService(questionId, selectComponentId),
    {
      manual: true,
      onSuccess(res) {
        setStat(res.data.stat);
      },
    }
  );
  useEffect(() => {
    if (selectComponentId) {
      run(id, selectComponentId);
    }
  }, [id, selectComponentId]);
  function genStatElem() {
    if (!selectComponentId) return <div>未选中组件</div>;
    const { StatComponent } = getComponentConfByType(selectComponentType) || {};
    if (StatComponent == null) return <div>该组件无统计图表</div>;
    return <StatComponent stat={stat} />;
  }
  return (
    <>
      <Title level={3}>图表统计</Title>
      <div>{genStatElem()}</div>
    </>
  );
};
export default ChartStat;
