import { useState } from "react";
import { useGetComponentInfo } from "../../../hooks/useGetComponentInfo";
import { getComponentConfByType } from "../../../components/QuestionComponents";
import classNames from "classnames";
import "./ComponentList.scss";
type PropsType = {
  selectComponentId: string;
  setSelectComponentId: (id: string) => void;
  setSelectComponentType: (type: string) => void;
};
const ComponentList = (props: PropsType) => {
  const { selectComponentId, setSelectComponentId, setSelectComponentType } =
    props;
  const { componentList } = useGetComponentInfo();

  return (
    <div className="ComponentList_container">
      {componentList
        .filter((c) => !c.isHidden)
        .map((c) => {
          const { fe_id, props, type } = c;
          const componentConf = getComponentConfByType(type);
          if (componentConf == null) return null;
          const { Component } = componentConf;
          const classes = classNames(
            "component_wrapper",
            selectComponentId === fe_id && "selected"
          );
          return (
            <div
              className={classes}
              key={fe_id}
              onClick={() => {
                setSelectComponentId(fe_id);
                setSelectComponentType(type);
              }}
            >
              <div className="component">
                <Component {...props} />
              </div>
            </div>
          );
        })}
    </div>
  );
};
export default ComponentList;
