import { useNavigate, useParams } from "react-router-dom";
import useGetPageInfo from "../../../hooks/useGetPageInfo";
import "./StatHeader.scss";
import {
  Button,
  Input,
  InputRef,
  Popover,
  QRCode,
  Space,
  Tooltip,
  message,
} from "antd";
import { CopyOutlined, LeftOutlined, QrcodeOutlined } from "@ant-design/icons";
import Title from "antd/es/typography/Title";
import { useRef } from "react";
const StatHeader = () => {
  const nav = useNavigate();
  const urlInputRef = useRef<InputRef>(null);
  const { id } = useParams();
  const { title, isPublished } = useGetPageInfo();
  function copy() {
    const elem = urlInputRef.current;
    if (elem == null) return;
    elem.select();
    document.execCommand("copy");
    message.success("复制成功");
  }
  function genLinkAndQRCodeElem() {
    const url = `http://localhost:3000/question/${id}`;
    const QRCodeElem = (
      <div style={{ textAlign: "center" }}>
        <QRCode value={url} size={150} />
      </div>
    );
    return (
      <Space>
        <Input value={url} style={{ width: "300px" }} ref={urlInputRef}></Input>
        <Tooltip title={"复制链接"}>
          <Button icon={<CopyOutlined />} onClick={copy}></Button>
        </Tooltip>
        <Popover content={QRCodeElem}>
          <Button icon={<QrcodeOutlined />}></Button>
        </Popover>
      </Space>
    );
  }
  return (
    <div>
      <div className="StatHeader_wrapper">
        <div className="header">
          <div className="left">
            <Space style={{ position: "relative", top: "-5px" }}>
              <Button
                type="link"
                icon={<LeftOutlined />}
                onClick={() => nav(-1)}
              >
                返回
              </Button>
              <Title>{title}</Title>
            </Space>
          </div>
          <div className="main">{genLinkAndQRCodeElem()}</div>
          <div className="right">
            <Button
              type="primary"
              size="small"
              onClick={() => nav(`/question/edit/${id}`)}
            >
              编辑
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};
export default StatHeader;
