import { useRequest } from "ahooks";
import { useState } from "react";
import { useParams } from "react-router-dom";
import { getQuestionStatListService } from "../../../services/stat";
import Title from "antd/es/typography/Title";
import { Pagination, Spin, Table } from "antd";
import { useGetComponentInfo } from "../../../hooks/useGetComponentInfo";
import { set } from "lodash";

type PropsType = {
  selectComponentId: string;
  setSelectComponentId: (id: string) => void;
  setSelectComponentType: (type: string) => void;
};
const PageStat = (props: PropsType) => {
  const { selectComponentId, setSelectComponentId, setSelectComponentType } =
    props;
  const { id = "" } = useParams();
  const [list, setList] = useState([]);
  const [total, setTotal] = useState(0);
  const [pageSize, setPageSize] = useState(10);
  const [page, setPage] = useState(1);
  const { loading } = useRequest(
    async () => {
      const res = await getQuestionStatListService(id, {
        page,
        pageSize,
      });
      return res.data;
    },
    {
      refreshDeps: [page, pageSize, id],
      onSuccess(res) {
        const { list, total } = res;
        setList(list);
        setTotal(total);
      },
    }
  );
  const { componentList } = useGetComponentInfo();
  const columns = componentList.map((item) => {
    const { fe_id, title, props = {}, type } = item;
    const colTitle = props.title || title;
    return {
      title: (
        <div
          style={{ cursor: "pointer" }}
          onClick={() => {
            setSelectComponentId(fe_id);
            setSelectComponentType(type);
          }}
        >
          <span style={{ color: fe_id === selectComponentId ? "#1900ff" : "" }}>
            {colTitle}
          </span>
        </div>
      ),
      dataIndex: fe_id,
    };
  });
  const dataSource = list.map((i: any) => ({ ...i, key: i._id }));
  const TableElem = (
    <>
      <Table
        columns={columns}
        dataSource={dataSource}
        pagination={false}
      ></Table>
      <div style={{ textAlign: "center", marginTop: "18px" }}>
        <Pagination
          total={total}
          pageSize={pageSize}
          current={page}
          onChange={(page) => setPage(page)}
          onShowSizeChange={(page, pageSize) => {
            setPage(page);
            setPageSize(pageSize);
          }}
        ></Pagination>
      </div>
    </>
  );
  return (
    <div>
      <Title level={3}>答卷数量：{!loading && total}</Title>
      {loading && (
        <div className="spinStyle">
          <Spin></Spin>
        </div>
      )}
      {!loading && TableElem}
    </div>
  );
};

export default PageStat;
