import { Button, Result, Spin } from "antd";
import { useLoadQuestionData } from "../../../hooks/useLoadQuestionData";
import useGetPageInfo from "../../../hooks/useGetPageInfo";
import { useNavigate } from "react-router-dom";
import { useTitle } from "ahooks";
import "./Stat.scss";
import StatHeader from "./StatHeader";
import ComponentList from "./ComponentList";
import { useState } from "react";
import PageStat from "./PageStat";
import ChartStat from "./ChartStat";
const Stat = () => {
  const navigate = useNavigate();
  const { loading } = useLoadQuestionData();
  const { isPublished, title } = useGetPageInfo();
  const [selectComponentId, setSelectComponentId] = useState("");
  const [selectComponentType, setSelectComponentType] = useState("");
  useTitle(`问卷统计 - ${title}`);
  const LoadingElem = (
    <div style={{ textAlign: "center", marginBottom: "60px" }}>
      <Spin></Spin>
    </div>
  );
  function genContentElem() {
    if (typeof isPublished === "boolean" && !isPublished) {
      return (
        <div style={{ flex: 1 }}>
          <Result
            status="warning"
            title="该页面还未发布"
            extra={
              <Button type="primary" onClick={handleClick}>
                返回
              </Button>
            }
          />
        </div>
      );
    }

    return (
      <>
        <div className="left">
          <ComponentList
            selectComponentId={selectComponentId}
            setSelectComponentId={setSelectComponentId}
            setSelectComponentType={setSelectComponentType}
          ></ComponentList>
        </div>
        <div className="main">
          <PageStat
            selectComponentId={selectComponentId}
            setSelectComponentId={setSelectComponentId}
            setSelectComponentType={setSelectComponentType}
          ></PageStat>
        </div>
        <div className="right">
          <ChartStat
            selectComponentId={selectComponentId}
            selectComponentType={selectComponentType}
          ></ChartStat>
        </div>
      </>
    );
  }

  const handleClick = () => {
    navigate(-1);
  };

  return (
    <div className="Stat_container">
      <StatHeader></StatHeader>
      <div className="content_wrapper">
        {loading && LoadingElem}
        <div className="content">{!loading && genContentElem()}</div>
      </div>
    </div>
  );
};

export default Stat;
